import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: () => import("@/views/index")
    },
    {
      path: '/list',
      component: () => import("@/components/list")
    },
  ]
})



